// make sure all space is available and the best approach is min-height to all the content based on view port
html, body {
    margin: 0;
    padding: 0;
    min-height: 100vh;
}

// --------------------------------------------------------------------------------------  CSS RESET
//                                                https://dev.to/hankchizljaw/a-modern-css-reset-6p3

// Make sure, commputed sizes don't get messed up.
*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    scroll-behavior: smooth; // when clicking an anchor, transition should be smooth.
    line-height: 1.5; // make room for accessible, readable text.
    // https://marco.org/2012/11/15/text-rendering-optimize-legibility
    text-rendering: optimizeSpeed;
}

// Don't allow elements to mess up with the margin;
h1,
h2,
h3,
h4,
h5,
h6,
p,
li,
figure,
figcaption,
blockquote,
dl,
dd {
    margin: 0;
}

// List elements with a class attribute get resetted, pure ones, don't.
ul,
ol {
    margin: 0;
    padding: 0;
    list-style: none;
}

// Make images easier to work with
img {
    max-width: 100%;
}

// Inherit fonts for inputs and buttons
input,
button,
textarea,
select {
    font: inherit;
}
